import { toast } from 'react-hot-toast';

//    
export const handleError = error => {
  let errorMessage = ' .    .';

  if (error.response) {
    //    ,      2xx
    switch (error.response.status) {
      case 400:
        errorMessage =
          ' .       .';
        break;
      case 401:
        errorMessage = '.  ,     .';
        break;
      case 403:
        errorMessage = '     .';
        break;
      case 404:
        errorMessage = '  .';
        break;
      case 500:
        errorMessage = ' .    .';
        break;
      default:
        errorMessage = ` : ${error.response.statusText}.`;
    }
  } else if (error.request) {
    //   ,     
    errorMessage =
      "  .   -'    .";
  } else {
    //     ,   
    errorMessage = `: ${error.message}`;
  }

  toast.error(errorMessage);
  return errorMessage;
};
